<template>
  <!-- ref -->
  <div class="wrapper" ref="wrapper">
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import bs from "better-scroll"
import BScroll from "@better-scroll/core"
import ObserveDOM from "@better-scroll/observe-dom"
import PullUp from "@better-scroll/pull-up"
import ObserveImage from '@better-scroll/observe-image'
BScroll.use(ObserveImage)
BScroll.use(PullUp)
BScroll.use(ObserveDOM)
export default {
  props: {
    probetype: {
      type: Number,
      default: 1
    },
    pullUpLoad: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      scroll: null
    }
  },
  mounted() {
    this.scroll = new bs(this.$refs.wrapper, {
      click: true,
      observeDOM: true,
      mouseWheel: true,
      // 
      probeType: this.probetype,
      pullUpLoad: this.pullUpLoad,
      observeImage: true
    })
    // home,top
    this.scroll.on("scroll", position => this.$emit("scroll", position))
    // 
    this.scroll.on("scroll", position => this.$emit("top", position))
    // 
    if (this.pullUpLoad) this.scroll.on("pullingUp", () => this.$emit("pullingUp"))
 
  },
  methods: {
    refresh() {
      this.scroll && this.scroll.refresh()
    } 
  }
}
</script>

<style scoped>
/* .content{
  overflow-y:scroll;
} */
</style>